import { Component } from 'react';
import TrackGainSlider from './TrackGainSlider';

export default class TrackGainSliderList extends Component {
  constructor(props){
    super();
    this.nSliders = props.inputAudio.length;
  }

  shouldComponentUpdate(nextProps, nextState){
    if (nextProps.inputAudio.length !== this.nSliders) return true;
    return false;
  }

  render(){
    const {gains, inputAudio, handler} = this.props;

    this.nSliders = inputAudio.length;

    const sliders = inputAudio.map(
       (e,i) => {
       return(
        <TrackGainSlider key={i} id={i} trackName={e.name} 
           initialGain={gains[i]} handler={handler} />
       );
      }
    );

    return (<div><center>{sliders}</center></div>);

  } // end render

}; // End class TrackGainSliders
